/* Style for the buttons with the base colours in the colour picker */

.base-colour {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    display: inline-block;
    margin: 0.5rem;
    box-shadow: -100px 0px rgba(176, 176, 176, 0.6);
    -moz-box-shadow: -100px 0px rgba(176, 176, 176, 0.6);
    -webkit-box-shadow: 1px 1px 1px 1px rgba(176, 176, 176, 0.6);
    -o-box-shadow: 0px 010px rgba(176, 176, 176, 0.6);
    cursor: pointer;
    margin-left: 1rem;
}

.base-colour:hover {
    transform: scale(1.3);
}