/*Style for the title*/

#title {
    margin-left: 24rem;
    margin-top: 2rem;
    color: #013521;
}


/*Style for the text that presents the summary of table content*/

#summary {
    color: #013521;
    font-family: 'Exo 2', sans-serif;
    margin-bottom: 3rem;
    font-weight: 700;
}


/*Style for the table*/

#table-container {
    width: 60%;
    margin: 0 auto;
    margin-top: 3rem;
}

table {
    width: 100%;
    margin: 0 auto;
}

th {
    background: #F2B705;
    color: #002517;
    font-family: 'Exo 2', sans-serif;
    font-weight: 700;
}

tr {
    font-family: 'Exo 2', sans-serif;
    font-weight: 400;
    background: #f8f1e8;
    border-bottom: 1px solid #FFF;
    margin-bottom: 5px;
    font-family: 'Exo 2', sans-serif;
}

tr:nth-child(even) {
    background: #ecdac6;
}

th,
td {
    text-align: left;
    padding: 20px;
    font-weight: 300;
}