/* View title */

#add-box-title {
    color: #F2B705;
}


/* Form styling */

#form-container {
    margin: 2rem;
    color: #013521;
    font-family: 'Exo 2', sans-serif;
    ;
}

.form-control {
    margin-bottom: 1rem;
}

.form-control input,
.form-control label {
    display: block;
}

.form-control label {
    font-weight: bold;
    margin-bottom: 0.5rem;
}

.form-control input,
.form-control select {
    font: inherit;
    padding: 0.5rem;
    border-radius: 4px;
    border: 1px solid #ccc;
    width: 20rem;
    max-width: 100%;
    box-shadow: 0.5px 0.5px rgba(169, 169, 169, 0.6);
    -moz-box-shadow: 0.5px 0.5px rgba(169, 169, 169, 0.6);
    -webkit-box-shadow: 0.5px 0.5px 0.5px 0.5px rgba(169, 169, 169, 0.6);
    -o-box-shadow: 0.5px 0.5px rgba(169, 169, 169, 0.6);
}

.form-control input:focus {
    outline: none;
    border-color: #00481f;
    background-color: #f8f1e8;
}

.control-group {
    display: flex;
    column-gap: 1rem;
    flex-wrap: wrap;
}

.control-group .form-control {
    min-width: 15rem;
    flex: 1;
}


/*Select style*/

select#country {
    font-size: 'Exo 2', sans-serif;
    background-color: #F2B705;
    color: #013521;
    width: 21rem !important;
}

select option {
    background-color: #fff;
}


/* Button styles */

button {
    font: inherit;
    background-color: #007533;
    color: white;
    border: 1px solid #007533;
    padding: 0.5rem 1.5rem;
    border-radius: 4px;
    cursor: pointer;
}

button:hover,
button:active {
    background-color: #00481f;
    border-color: #00481f;
}

.form-actions button {
    margin-left: 15rem;
    margin-top: 2rem;
    box-shadow: 1px 1px 1px 1px rgba(169, 169, 169, 0.6);
    -moz-box-shadow: 1px 1px 1px 1px rgba(169, 169, 169, 0.6);
    -webkit-box-shadow: 1px 1px 1px 1px rgba(169, 169, 169, 0.6);
    -o-box-shadow: 1px 1px 1px 1px rgba(169, 169, 169, 0.6);
}


/* Error message and invalid field */

.invalid input {
    border: 1px solid #b40e0e;
    background-color: #fddddd;
}

.invalid input:focus {
    border-color: #ff8800;
    background-color: #fbe8d2;
}

.error-text {
    color: #b40e0e;
}


/*Hide arrows in input number field*/

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type=number] {
    -moz-appearance: textfield;
}