/* Google fonts*/

@import url('https://fonts.googleapis.com/css2?family=Exo+2:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Exo+2:wght@400;700&display=swap');

/* Styling for the nav-bar and its title*/
#nav-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #003824;
    width: 100%;
    height: 6rem;
}

#boxinator {
    margin: 0rem;
    color: white;
    font-family: 'Exo 2', sans-serif;
    font-size: 3.2rem;
    padding: 1rem;
    cursor: pointer;
}

#nav-btn {
    margin-right: 2rem !important;
    width: 7rem;
    height: 3rem;
    border-radius: 2rem;
    border-color: #fff;
    background-color: white;
    color: #003824;
    text-align: center;
    font-family: 'Exo 2', sans-serif;
    box-shadow: -100px 0px rgba(19, 19, 19, 0.6);
    -moz-box-shadow: -100px 0px rgba(19, 19, 19, 0.6);
    -webkit-box-shadow: 1px 1px 1px 1px rgba(19, 19, 19, 0.6);
    -o-box-shadow: 0px 010px rgba(19, 19, 19, 0.6);
}

#nav-btn:hover {
    background-color: #00481f;
    color: #FFF;
    border-color: #00481f;
}