/* General style for the colour picker component */

#colour-picker {
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
    border-radius: 6px;
    background-color: white;
    padding: 1rem;
    width: 30rem;
    z-index: 10;
    position: fixed;
    top: 20vh;
    left: calc(50% - 15rem);
    box-shadow: 3px 3px rgba(53, 53, 53, 0.6);
    -moz-box-shadow: 3px 3px rgba(53, 53, 53, 0.6);
    -webkit-box-shadow: 3px 3px 3px 3px rgba(53, 53, 53, 0.6);
    -o-box-shadow: 3px 3px rgba(53, 53, 53, 0.6);
}


/*Styling for the colour picker's buttons */

#colour-btn {
    margin: 1rem;
    box-shadow: 1px 1px rgba(169, 169, 169, 0.6);
    -moz-box-shadow: 1px 1px rgba(169, 169, 169, 0.6);
    -webkit-box-shadow: 1px 1px 1px 1px rgba(169, 169, 169, 0.6);
    -o-box-shadow: 1px 1px rgba(169, 169, 169, 0.6);
}

#colour-btn-container {
    text-align: right;
}

#colour-btn:disabled {
    opacity: 0.3;
}


/* Style for the React sliders and their connected values*/

.range {
    margin-left: 2.2rem;
    margin-bottom: 4rem;
}

.css-3g5hux-Slider {
    background-color: #F2B705 !important;
}

.value {
    color: #000;
    font-size: 12px;
    margin-top: 20px;
    position: absolute;
}

.left {
    left: 3rem;
}

.right {
    right: 17rem;
}


/* Styling for the titles in the colour picker */

.hsl-title,
h5 {
    margin-left: 1rem;
    text-align: left;
}


/* Style to position elements inside the colour picker */

.flex-container-1 {
    display: flex;
    align-items: center;
}

.flex-container-3 .selected-colour {
    text-align: right;
}